<script setup lang="ts">
import useAsync from "~/composables/use-async";
import SubscriptionAPI from "~/services/subscription";
import formatDate from "~/utils/format-date";
import getErrorLocalePathMessage from "~/utils/locale";
import { getAccessLabels, getDurationLabel, getPriceLabel } from "~/utils/subscription";

const { t, te } = useI18n();
const route = useRoute();
const router = useRouter();
const toaster = useToast();
const getFormattedDate = (date: Date | number) => formatDate(date, "d.L.yyyy");
const {
  data: subscription,
  loading,
  error,
  run: loadUserSubscription,
} = useAsync(() => SubscriptionAPI.loadUserSubscriptionById(route.params.id as string));
const onSuccessCancel = () => {
  toaster.success(t("pages.account_subscription.alert_subscription_cancel_success"));
  router.push("/account/subscription/");
};
const onErrorCancel = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};
const { loading: cancellingSubscription, run: cancelUserSubscription } = useAsync(
  () => SubscriptionAPI.cancelUserSubscription(route.params.id as string),
  { onSuccess: onSuccessCancel, onError: onErrorCancel },
);

watch(error, () => showError({ statusCode: 404, data: { context: "page" } }));
watch(() => route.params.id, loadUserSubscription, { immediate: true });
</script>

<template>
  <div>
    <CardSubscriptionDetailSkeleton v-if="loading" />

    <CardSubscriptionDetail
      v-else-if="subscription"
      :title="subscription.title"
      :description="subscription.description"
      :price="getPriceLabel(subscription.price, subscription.currency)"
      :expiration-date="getFormattedDate(subscription.endDate)"
      :last-payment-date="getFormattedDate(subscription.lastPayment)"
      :active="subscription.isActive && !subscription.isCanceled"
      :disabled="cancellingSubscription"
      :auto-renew="subscription.autoRenew"
      :duration-label="getDurationLabel(subscription)"
      :access-labels="getAccessLabels(subscription.type)"
      :allowed-access-identity-items="subscription.allowedAccessIdentityItems"
      @click-cancel="cancelUserSubscription()"
    />

    <div class="mt-8 text-center">
      <BaseButton tag="nuxt-link" to="/account/subscription/" type="secondary" name="billing-tab" link>
        {{ t('pages.account_subscription.button_back_to_my_subscription') }}
      </BaseButton>
    </div>
  </div>
</template>
